import {Button} from 'react-mdl'
import {connect} from 'react-redux'
import {signIn} from '../lib/action-creators/auth'
import {FormHelper} from './form-helper'

const styles = require('./sign-in.scss')

const production = process.env.NODE_ENV === 'production'
const oauthUrl = service => `${production ? '' : 'http://localhost:7001'}/auth/${service}/callback`

export const SignIn = connect(
  state => ({fail: state.user.fail})
)(({fail, dispatch}) =>
  <div className={styles.back}>
    <div className={styles.logo}>
      <img src='assets/logo.png' className={styles.logo} />
      <h1>Manager</h1>
    </div>
    <div className={styles.box}>
      <FormHelper
        saveButton={'Logga in'}
        object={{}}
        onSave={credentials => dispatch(signIn(credentials))}
        fields={[
          fail && <p>Felaktigt användarnamn och/eller lösenord</p>,
          {label: 'Epost', path: ['email'], type: 'email'},
          {label: 'Lösenord', path: ['password'], type: 'password'},
        ]} />

      <form onSubmit={e => {
        e.preventDefault()
      }}>
        <p>
          Eller logga in med
        </p>
        <div className={styles.buttons}>
          <Button href={oauthUrl('facebook')} primary ripple raised>Facebook</Button>
        </div>
      </form>
    </div>

    <div className={styles.details}>
      Privacy policy
    </div>
  </div>
)
